import React, { FC, useState } from 'react';
import { DatetimePicker, Field, Popup } from 'rn-vant';
import { ScrollView, ViewStyle } from 'react-native';
import { DemoBlock } from '../../components';

const cardStyle: ViewStyle = {
  borderRadius: 8,
  marginHorizontal: 12,
};

const PickerDemo: FC = () => {
  const [fieldValue, setFieldValue] = useState('');
  const [showPicker, setShowPicker] = useState(false);

  return (
    <ScrollView>
      <DemoBlock title="">
        <DatetimePicker
          title=""
          type="date"
          minDate={new Date(2020, 0, 1)}
          maxDate={new Date(2025, 10, 1)}
          value={new Date()}
          style={cardStyle}
        />
      </DemoBlock>
      <DemoBlock title="">
        <DatetimePicker
          type="year-month"
          title=""
          minDate={new Date(2020, 0, 1)}
          maxDate={new Date(2025, 10, 1)}
          value={new Date()}
          style={cardStyle}
          formatter={(type: string, val: string) => {
            if (type === 'year') {
              return `${val}`;
            }
            if (type === 'month') {
              return `${val}`;
            }
            return val;
          }}
        />
      </DemoBlock>
      <DemoBlock title="">
        <DatetimePicker
          type="month-day"
          title=""
          minDate={new Date(2020, 0, 1)}
          maxDate={new Date(2025, 10, 1)}
          value={new Date()}
          style={cardStyle}
          formatter={(type: string, val: string) => {
            if (type === 'month') {
              return `${val}`;
            }
            if (type === 'day') {
              return `${val}`;
            }
            return val;
          }}
        />
      </DemoBlock>
      <DemoBlock title="">
        <DatetimePicker title="" type="time" minHour="10" maxHour="20" style={cardStyle} />
      </DemoBlock>
      <DemoBlock title="">
        <DatetimePicker
          type="datetime"
          title=""
          minDate={new Date(2020, 0, 1)}
          maxDate={new Date(2025, 10, 1)}
          value={new Date()}
          style={cardStyle}
        />
      </DemoBlock>
      <DemoBlock title="">
        <DatetimePicker
          type="datehour"
          title=""
          minDate={new Date(2020, 0, 1)}
          maxDate={new Date(2025, 10, 1)}
          value={new Date()}
          style={cardStyle}
        />
      </DemoBlock>
      <DemoBlock title="">
        <DatetimePicker
          title=""
          type="time"
          minHour="10"
          maxHour="20"
          value="12:00"
          filter={(type, options) => {
            if (type === 'minute') {
              return options.filter(option => +option % 5 === 0);
            }
            return options;
          }}
        />
      </DemoBlock>
      <DemoBlock title="">
        <DatetimePicker
          title=""
          type="date"
          columnsOrder={['month', 'day', 'year']}
          minDate={new Date(2020, 0, 1)}
          maxDate={new Date(2025, 10, 1)}
          value={new Date()}
        />
      </DemoBlock>
      <DemoBlock title="">
        <Field
          readonly
          label=""
          value={fieldValue}
          placeholder=""
          onPress={() => setShowPicker(true)}
        />
        <Popup round visible={showPicker} position="bottom" onClose={() => setShowPicker(false)}>
          <DatetimePicker
            type="datetime"
            onConfirm={(value: string) => {
              setFieldValue(value);
              setShowPicker(false);
            }}
            filter={(type: string, options) => {
              if (type === 'minute') {
                return options.filter(option => +option % 5 === 0);
              }
              return options;
            }}
            minDate={new Date(2021, 0, 1)}
            maxDate={new Date(2021, 2, 1)}
            value={fieldValue}
          />
        </Popup>
      </DemoBlock>
    </ScrollView>
  );
};

export default PickerDemo;
