import React, { memo } from 'react';
import { ScrollView } from 'react-native';
import { Selector } from 'rn-vant';
import { DemoBlock } from '../../components';

const options = [
  {
    label: '',
    value: '1',
  },
  {
    label: '',
    value: '2',
  },
  {
    label: '',
    value: '3',
  },
];

const SelectorExample = memo(() => {
  return (
    <ScrollView style={{ backgroundColor: 'white' }}>
      <DemoBlock title="" inset>
        <Selector
          options={options}
          defaultValue={['1']}
          onChange={(arr, extend) => console.log(arr, extend.items)}
        />
      </DemoBlock>
      <DemoBlock title="" inset>
        <Selector
          options={options}
          defaultValue={['2', '3']}
          multiple
          onChange={(arr, extend) => console.log(arr, extend.items)}
        />
      </DemoBlock>
      <DemoBlock title="" inset>
        <Selector
          disabled
          defaultValue={['1']}
          options={[
            {
              label: '',
              value: '1',
            },
            {
              label: '',
              value: '2',
              disabled: true,
            },
            {
              label: '',
              value: '3',
            },
          ]}
        />
      </DemoBlock>
      <DemoBlock title="" inset>
        <Selector
          options={[
            {
              label: '',
              value: '1',
              description: '',
            },
            {
              label: '',
              value: '2',
              description: '',
            },
          ]}
        />
      </DemoBlock>
    </ScrollView>
  );
});

export default SelectorExample;
