import React, { memo, useContext } from 'react';
import { ScrollView, Text, ViewStyle, View } from 'react-native';
import { Tabs } from 'rn-vant';
import { GlobalContext } from '../../GlobalContext';
import { DemoBlock } from '../../components';

const TabExample = memo(() => {
  const { themeVars } = useContext(GlobalContext);
  const containerStyle: ViewStyle = {
    paddingVertical: 24,
    paddingHorizontal: 20,
    backgroundColor: themeVars.background_2,
  };

  const renderContent = (count = 3) =>
    Array(count)
      .fill(null)
      .map((_, idx) => (
        // eslint-disable-next-line react/no-array-index-key
        <Tabs.TabPane key={idx} title={`${idx + 1}`}>
          <View style={containerStyle}>
            <Text> {idx + 1}</Text>
          </View>
        </Tabs.TabPane>
      ));

  return (
    <ScrollView>
      <DemoBlock title="">
        <Tabs>{renderContent()}</Tabs>
      </DemoBlock>
      <DemoBlock title="">
        <Tabs>{renderContent(8)}</Tabs>
      </DemoBlock>
      <DemoBlock title="">
        <Tabs>
          <Tabs.TabPane title="1">
            <View style={containerStyle}>
              <Text> 1</Text>
            </View>
          </Tabs.TabPane>
          <Tabs.TabPane title="2" disabled>
            <View style={containerStyle}>
              <Text> 2</Text>
            </View>
          </Tabs.TabPane>
          <Tabs.TabPane title="3">
            <View style={containerStyle}>
              <Text> 3</Text>
            </View>
          </Tabs.TabPane>
        </Tabs>
      </DemoBlock>
      <DemoBlock title="">
        <Tabs shrink>{renderContent(4)}</Tabs>
      </DemoBlock>
    </ScrollView>
  );
});

export default TabExample;
