import React from 'react';
import { Form, Cell, Input } from 'rn-vant';
import { DemoBlock } from '../../components';

const sleep = (ms: number) => new Promise(resolve => setTimeout(resolve, ms));

export default () => {
  return (
    <DemoBlock title="">
      <Form>
        <Cell.Group inset>
          <Form.Item
            name="text1"
            label=""
            rules={{ pattern: { value: /\d{6}/, message: '6' } }}
          >
            <Input placeholder="" />
          </Form.Item>
          <Form.Item
            name="text2"
            label=""
            rules={{
              validate: value => {
                return /1\d{10}/.test(value) || '';
              },
            }}
          >
            <Input placeholder="" />
          </Form.Item>
          <Form.Item
            name="text3"
            label=""
            rules={{
              validate: async value => {
                await sleep(1000);
                return /\d{6}/.test(value) || '';
              },
            }}
          >
            <Input placeholder="" />
          </Form.Item>
        </Cell.Group>
      </Form>
    </DemoBlock>
  );
};
