import React from 'react';
import { Form, Cell, Radio, Space, Input, NoticeBar } from 'rn-vant';
import { DemoBlock } from '../../components';

export default () => {
  return (
    <DemoBlock title="Form.Subscribe">
      <Form defaultValues={{ type: 'mobile', account: '18888888888' }}>
        <Cell.Group inset>
          <Form.Item name="type" label="">
            <Radio.Group direction="horizontal">
              <Space>
                <Radio value="mobile"></Radio>
                <Radio value="email"></Radio>
              </Space>
            </Radio.Group>
          </Form.Item>
          <Form.Subscribe to="type">
            {type => {
              return (
                <>
                  {type === 'mobile' && (
                    <Form.Item name="account" label="">
                      <Input placeholder="" />
                    </Form.Item>
                  )}
                  {type === 'email' && (
                    <Form.Item name="account" label="">
                      <Input placeholder="" />
                    </Form.Item>
                  )}
                </>
              );
            }}
          </Form.Subscribe>
          <Form.Subscribe to={['type', 'account']}>
            {([type, account]) => (
              <NoticeBar>
                 {type === 'mobile' ? '' : ''} {account} 
              </NoticeBar>
            )}
          </Form.Subscribe>
        </Cell.Group>
      </Form>
    </DemoBlock>
  );
};
