import React, { memo } from 'react';
import { ScrollView } from 'react-native';
import { Dialog, Cell, Toast } from 'rn-vant';
import { DemoBlock } from '../../components';

const DialogExample = memo(() => {
  const showDialog = () => {
    Dialog.alert({
      title: '',
      message: '',
    }).then(() => {
      // on close
    });
  };

  const showNoTitleDialog = () => {
    Dialog.alert({
      message: '',
    }).then(() => {
      // on close
    });
  };

  const showConfirm = () => {
    Dialog.confirm({
      title: '',
      message: '',
    })
      .then(() => {
        // on confirm
      })
      .catch(() => {
        // on cancel
      });
  };

  const showAsyncConfirm = () => {
    Dialog.show({
      title: '',
      message: '',
      showCancelButton: true,
      onCancel: () => {
        return new Promise(res => {
          setTimeout(() => {
            res(true);
            Toast.success({ message: '' });
          }, 3000);
        });
      },
      onConfirm: () => {
        return new Promise(res => {
          setTimeout(() => {
            res(true);
            Toast.success({ message: '' });
          }, 3000);
        });
      },
    });
  };

  return (
    <ScrollView>
      <DemoBlock title="">
        <Cell.Group inset>
          <Cell title="" isLink onPress={showDialog} />
          <Cell title="" isLink onPress={showNoTitleDialog} />
          <Cell title="" isLink onPress={showConfirm} />
        </Cell.Group>
      </DemoBlock>
      <DemoBlock title="">
        <Cell.Group inset>
          <Cell
            title=""
            isLink
            onPress={() => {
              Dialog.alert({
                title: '',
                message: '',
                theme: 'round-button',
              });
            }}
          />
          <Cell
            title=""
            isLink
            onPress={() => {
              Dialog.alert({
                message: '',
                theme: 'round-button',
              });
            }}
          />
        </Cell.Group>
      </DemoBlock>
      <DemoBlock title="">
        <Cell.Group inset>
          <Cell title="" isLink onPress={showAsyncConfirm} />
        </Cell.Group>
      </DemoBlock>
    </ScrollView>
  );
});

export default DialogExample;
