import React, { memo, useState } from 'react';
import { Notify, Cell, Icon } from 'rn-vant';
import { ScrollView, Text } from 'react-native';
import { DemoBlock } from '../../components';

const NotifyExample = memo(() => {
  const [show, setShow] = useState(false);

  const showNotify = () => {
    setShow(true);
    setTimeout(() => {
      setShow(false);
    }, 2000);
  };

  return (
    <ScrollView>
      <DemoBlock title="">
        <Cell.Group inset>
          <Cell title="" isLink onPress={() => Notify.show('')} />
        </Cell.Group>
      </DemoBlock>
      <DemoBlock title="">
        <Cell.Group inset>
          <Cell
            title=""
            isLink
            onPress={() => Notify.show({ type: 'primary', message: '' })}
          />
          <Cell
            title=""
            isLink
            onPress={() => Notify.show({ type: 'success', message: '' })}
          />
          <Cell
            title=""
            isLink
            onPress={() => Notify.show({ type: 'danger', message: '' })}
          />
          <Cell
            title=""
            isLink
            onPress={() => Notify.show({ type: 'warning', message: '' })}
          />
        </Cell.Group>
      </DemoBlock>
      <DemoBlock title="">
        <Cell.Group inset>
          <Cell
            title=""
            isLink
            onPress={() =>
              Notify.show({ color: '#ad0000', background: '#ffe1e1', message: '' })
            }
          />
          <Cell
            title=""
            isLink
            onPress={() => Notify.show({ message: '', duration: 1000 })}
          />
        </Cell.Group>
      </DemoBlock>
      <DemoBlock title="">
        <Cell.Group inset>
          <Cell title="" isLink onPress={showNotify}>
            <Notify type="success" visible={show}>
              <Icon name="bell" style={{ marginRight: 4 }} color="white" size={14} />
              <Text></Text>
            </Notify>
          </Cell>
        </Cell.Group>
      </DemoBlock>
    </ScrollView>
  );
});

export default NotifyExample;
