import React, { memo, useState } from 'react';
import { ScrollView, View, Text } from 'react-native';
import { Cell, ActionSheet, ActionSheetAction } from 'rn-vant';
import { DemoBlock } from '../../components';

const defaultActions: ActionSheetAction[] = [
  { name: '' },
  { name: '' },
  { name: '' },
];
const actions1: ActionSheetAction[] = [
  { name: '' },
  { name: '' },
  { name: '', subname: '' },
];
const actions2: ActionSheetAction[] = [
  { name: '', color: '#ee0a24' },
  { name: '', disabled: true },
  { name: '', loading: true },
];

type OpenParams = {
  list?: ActionSheetAction[];
  cancel?: string;
  desc?: string;
  useNative?: boolean;
};

const ActionSheetExample = memo(() => {
  const [actions, setActions] = useState(defaultActions);
  const [visible, setVisible] = useState(false);
  const [visible1, setVisible1] = useState(false);
  const [cancelText, setCancelText] = useState('');
  const [description, setDescription] = useState('');
  const [useNativeIOS, setUseNativeIOS] = useState(false);

  const onOpen = (params?: OpenParams) => {
    setCancelText(params?.cancel ?? '');
    setDescription(params?.desc ?? '');
    setActions(params?.list ?? defaultActions);
    setUseNativeIOS(params?.useNative ?? false);
    setVisible(true);
  };

  const onClose = () => {
    setCancelText('');
    setDescription('');
    setActions(defaultActions);
    setUseNativeIOS(false);
    setVisible(false);
  };

  return (
    <ScrollView>
      <DemoBlock title="">
        <Cell.Group inset>
          <Cell title="" isLink onPress={onOpen} />
          <Cell title="" isLink onPress={() => onOpen({ cancel: '' })} />
          <Cell
            title=""
            isLink
            onPress={() => onOpen({ desc: '', cancel: '', list: actions1 })}
          />
        </Cell.Group>
      </DemoBlock>
      <DemoBlock title="">
        <Cell.Group inset>
          <Cell
            title=""
            isLink
            onPress={() => onOpen({ cancel: '', list: actions2 })}
          />
        </Cell.Group>
      </DemoBlock>
      <DemoBlock title="">
        <Cell.Group inset>
          <Cell title="" isLink onPress={() => setVisible1(true)} />
        </Cell.Group>
      </DemoBlock>
      <DemoBlock title="IOS ">
        <Cell.Group inset>
          <Cell
            title="IOS "
            isLink
            onPress={() => onOpen({ useNative: true, cancel: '', desc: '' })}
          />
        </Cell.Group>
      </DemoBlock>

      <ActionSheet
        visible={visible}
        actions={actions}
        onClose={onClose}
        cancelText={cancelText}
        description={description}
        useNativeIOS={useNativeIOS}
      />

      <ActionSheet visible={visible1} onClose={() => setVisible1(false)} title="" closeable>
        <View>
          <Text style={{ paddingHorizontal: 16, paddingTop: 16, paddingBottom: 160 }}></Text>
        </View>
      </ActionSheet>
    </ScrollView>
  );
});

export default ActionSheetExample;
