import React, { useRef } from 'react';
import { Form, Cell, Input, FormInstance, Button } from 'rn-vant';
import { DemoBlock } from '../../components';

interface FormValue {
  username: string;
  password: string;
}

export default () => {
  const formRef = useRef<FormInstance<FormValue>>(null);

  return (
    <DemoBlock title="">
      <Form<FormValue> form={formRef}>
        <Cell.Group inset>
          <Form.Item label="" name="username" rules={{ required: '' }}>
            <Input placeholder="" />
          </Form.Item>
          <Form.Item label="" name="password" rules={{ required: '' }}>
            <Input placeholder="" type="password" />
          </Form.Item>
        </Cell.Group>
      </Form>
      <Button round type="primary" style={{ marginHorizontal: 16, marginTop: 16 }}>
        
      </Button>
    </DemoBlock>
  );
};
