import React, { memo, useState } from 'react';
import { ScrollView } from 'react-native';
import { Switch, Cell } from 'rn-vant';
import { DemoBlock } from '../../components';

const SwitchExample = memo(() => {
  const [value] = useState(false);

  return (
    <ScrollView>
      <DemoBlock title="" inset>
        <Switch defaultChecked onChange={checked => console.log(`switch to ${checked}`)} />
      </DemoBlock>
      <DemoBlock title="" inset>
        <Switch disabled defaultChecked />
      </DemoBlock>
      <DemoBlock title="" inset>
        <Switch loading defaultChecked />
      </DemoBlock>
      <DemoBlock title="" inset>
        <Switch size={24} defaultChecked />
      </DemoBlock>
      <DemoBlock title="" inset>
        <Switch activeColor="#ee0a24" inactiveColor="#dcdee0" defaultChecked />
      </DemoBlock>
      <DemoBlock title="" inset>
        <Switch checked={value} />
      </DemoBlock>
      <DemoBlock title="" inset>
        <Cell title="" value={<Switch defaultChecked />} center />
      </DemoBlock>
    </ScrollView>
  );
});

export default SwitchExample;
