import React, { FC, useState } from 'react';
import { Picker, Toast, Popup, Field } from 'rn-vant';
import { ScrollView, ViewStyle } from 'react-native';
import { DemoBlock } from '../../components';

const columns = [
  { text: '', value: 'Hangzhou' },
  { text: '', value: 'Ningbo' },
  { text: '', value: 'Wenzhou' },
  { text: '', value: 'Shaoxing' },
  { text: '', value: 'Huzhou' },
];

const multiColumns = [
  // 
  [
    { text: '', value: 'Monday' },
    { text: '', value: 'Tuesday' },
    { text: '', value: 'Wednesday' },
    { text: '', value: 'Thursday' },
    { text: '', value: 'Friday' },
  ],
  // 
  [
    { text: '', value: 'Morning' },
    { text: '', value: 'Afternoon' },
    { text: '', value: 'Evening' },
  ],
];

const cascadeColumns = [
  {
    text: '',
    value: 'Zhejiang',
    children: [
      {
        text: '',
        value: 'Hangzhou',
        children: [
          { text: '', value: 'Xihu' },
          { text: '', value: 'Yuhang' },
        ],
      },
      {
        text: '',
        value: 'Wenzhou',
        children: [
          { text: '', value: 'Lucheng' },
          { text: '', value: 'Ouhai' },
        ],
      },
    ],
  },
  {
    text: '',
    value: 'Fujian',
    children: [
      {
        text: '',
        value: 'Fuzhou',
        children: [
          { text: '', value: 'Gulou' },
          { text: '', value: 'Taijiang' },
        ],
      },
      {
        text: '',
        value: 'Xiamen',
        children: [
          { text: '', value: 'Siming' },
          { text: '', value: 'Haicang' },
        ],
      },
    ],
  },
];

const cardStyle: ViewStyle = {
  borderRadius: 8,
  marginHorizontal: 12,
};

const PickerDemo: FC = () => {
  const [showPicker, setShowPicker] = useState(false);
  const [pickerValue, setPickerValue] = useState('');

  const onChange = (value: string, _: any, index: number) => {
    Toast(`${value}, ${index}`);
  };

  const handleFileChange = (value: string) => {
    setPickerValue(value);
    setShowPicker(false);
  };

  return (
    <ScrollView>
      <DemoBlock title="">
        <Picker
          title=""
          columns={columns}
          onChange={onChange}
          onCancel={() => Toast.info('')}
          onConfirm={() => Toast.info('')}
          style={cardStyle}
        />
      </DemoBlock>
      <DemoBlock title="">
        <Field
          label=""
          placeholder=""
          isLink
          readOnly
          onPress={() => setShowPicker(true)}
          value={pickerValue}
        />
        <Popup round position="bottom" visible={showPicker} onClose={() => setShowPicker(false)}>
          <Picker
            title=""
            columns={columns}
            onCancel={() => setShowPicker(false)}
            onConfirm={handleFileChange}
            value={pickerValue}
          />
        </Popup>
      </DemoBlock>
      <DemoBlock title="">
        <Picker title="" columns={multiColumns} style={cardStyle} />
      </DemoBlock>
      <DemoBlock title="">
        <Picker title="" columns={cascadeColumns} style={cardStyle} />
      </DemoBlock>
    </ScrollView>
  );
};

export default PickerDemo;
