import React, { FC, useRef } from 'react';
import { Button, Popover, Toast, PopoverAction, Space, Grid, PopoverInstance } from 'rn-vant';
import { ScrollView } from 'react-native';
import { AddO, MusicO, MoreO, PhotoO } from '@rn-vant/icons';
import { DemoBlock } from '../../components';

const actions: PopoverAction[] = [{ text: '' }, { text: '' }, { text: '' }];
const disabledActions: PopoverAction[] = [
  { text: '', disabled: true },
  { text: '', disabled: true },
  { text: '' },
];
const iconActions: PopoverAction[] = [
  { text: '', icon: <AddO /> },
  { text: '', icon: <MusicO /> },
  { text: '', icon: <MoreO /> },
];

const PopoverDemo: FC = () => {
  const popover = useRef<PopoverInstance>(null);
  const select = (option: PopoverAction) => Toast.info(option.text);

  return (
    <ScrollView>
      <DemoBlock title="" inset>
        <Space>
          <Popover
            actions={actions}
            onSelect={select}
            reference={<Button type="primary"></Button>}
          />
          <Popover
            actions={actions}
            theme="dark"
            onSelect={select}
            reference={<Button type="primary"></Button>}
          />
        </Space>
      </DemoBlock>
      <DemoBlock title="" inset>
        <Space>
          <Popover
            actions={iconActions}
            onSelect={select}
            reference={<Button type="primary"></Button>}
          />
          <Popover
            actions={disabledActions}
            onSelect={select}
            reference={<Button type="primary"></Button>}
          />
        </Space>
      </DemoBlock>
      <DemoBlock title="" inset>
        <Space>
          <Popover ref={popover} reference={<Button type="primary"></Button>}>
            <Grid border={false} columnNum={3} square style={{ width: 240 }}>
              {Array.from({ length: 6 }, (_, i) => (
                <Grid.Item
                  onPress={() => popover.current?.hide()}
                  key={i}
                  icon={<PhotoO />}
                  text=""
                />
              ))}
            </Grid>
          </Popover>
        </Space>
      </DemoBlock>
    </ScrollView>
  );
};

export default PopoverDemo;
